/* global  Swiper:false */

//slider

(function () {

	"use strict";
	var testimonialSwiper2 = new Swiper(".q-section21-testimonial-container", {
		slidesPerView: "auto",
		observer: true,
		observeParents: true,
		centeredSlides: false,
		spaceBetween: 30,
		autoplay: false,
		breakpoints:{
			720:
			{
				spaceBetween: 0,
			}
		}
	});

	document.querySelector(".q-section-21-slide-0").addEventListener("click", function () {
		testimonialSwiper2.slideTo(0, 400);
	});
	document.querySelector(".q-section-21-slide-1").addEventListener("click", function () {
		testimonialSwiper2.slideTo(1, 400);
	});
	document.querySelector(".q-section-21-slide-2").addEventListener("click", function () {
		testimonialSwiper2.slideTo(2, 400);
	});

	var slideTabs = document.querySelectorAll(".slide-tab");

	testimonialSwiper2.on("slideChange", function ( ) {
		var activeTab = document.querySelector(".q-section-21-slide-" + testimonialSwiper2.activeIndex );
		Array.prototype.forEach.call(slideTabs, function(el){
			el.classList.remove("q-section21-active-slide-tab");
			activeTab.classList.add("q-section21-active-slide-tab");
		});
	});
}());

//slider
